import { Component, ViewEncapsulation, Injector } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { Codes } from '../../../utils';
import { NavigationService, ONavigationItem } from '../../navigation.service';

@Component({
  moduleId: module.id,
  selector: 'o-error-403',
  template: `
    <div fxFlex fxFlexFill fxLayout="column" fxLayoutAlign="center center">
      {{ 'MESSAGES.ERROR_403_TEXT' | oTranslate }}
      <button color="accent" type="button" mat-raised-button (click)="onNavigateBackClick()">
        {{ 'MESSAGES.ERROR_403_TEXT_BUTTON' | oTranslate }}
      </button>
    </div>
  `,
  styles: [`
    .o-error-403{height:100%}.o-error-403 button{margin:16px}
  `],
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-error-403]': 'true'
  }
})
export class Error403Component {

  protected router: Router;
  protected navigationService: NavigationService;
  protected lastPageData: ONavigationItem;

  constructor(protected injector: Injector) {
    this.router = this.injector.get(Router);
    this.navigationService = this.injector.get(NavigationService);
    this.lastPageData = this.navigationService.getLastItem();
  }

  onNavigateBackClick() {
    let extras: NavigationExtras = {};
    let route: string = '';
    if (this.lastPageData) {
      extras[Codes.QUERY_PARAMS] = this.lastPageData.queryParams;
      route = this.lastPageData.url;
    }
    this.router.navigate([route], extras);
  }
}

